:root {
  --dark-gray:#7d7d7d;
  --med-gray:#E5E5E5;
  --light-gray:#F3F3F3;        
}

@import url('https://fonts.googleapis.com/css2?family=Libre+Baskerville:wght@400;700&display=swap');
* {box-sizing: border-box;}
h1,h2,h3,p {
  margin: 0;
}

button {
  border: none;
  background-color: transparent;
}
button,h1,h2,h3,p,a {
  font-family: 'Libre Baskerville', serif;
}