.progress-line,.active-line {
    width: 100%;
    height: 2px;
}
.progress-line {
    background-color: var(--med-gray);
}
.active-line {
    background-color: black;
    transition: width ease-out 200ms;
}
.footer-content {
    display: flex;
    justify-content: space-between;
    padding: 3% 5%;
    align-items: center;
}

.footer-text {
    p {
        color: var(--dark-gray);
        font-size: 0.5rem;
        padding-top: 5px;
    }
}
.footer-buttons {
    display: flex;
    justify-content: space-between;
    width: 22%;
}
.footer-buttons svg {
    cursor: pointer;
}
footer {
  position: fixed;
  bottom: 0;
  width: 100vw;
  background-color: white;
  z-index: 4;
}

.disabled  g{
    stroke: var(--med-gray);
}

@media screen and (min-width:720px) {
    .footer-content {
        padding: 1% 4%;
    }
    .footer-buttons {
        width: 8%;
    }
}

@media screen and (min-width:700px) and (max-width:1100px) {
    .footer-buttons {
        width: 20%;
    }
}